@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Dela Gothic One";
  src: local("../fonts/Dela/Dela Gothic One Regular"), local("Dela-Gothic-One-Regular"), url("../fonts/Dela/DelaGothicOne-Regular.woff2") format("woff2"), url("../fonts/Dela/DelaGothicOne-Regular.woff") format("woff"), url("../fonts/Dela/DelaGothicOne-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
div,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  overflow-x: hidden;
}

h2,
p,
li {
  word-break: break-word;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  padding: 0;
}

select {
  appearance: none;
}

select:focus,
input:focus,
button:focus {
  outline: 1px solid #f4a9c0;
  border: 1px solid #f4a9c0;
}

div {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.timeline {
  display: none;
}
@media (min-width: 1440px) {
  .timeline {
    position: fixed;
    z-index: 999;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    right: 0;
    top: 30%;
    bottom: 0;
    left: auto;
    margin: auto;
  }
}
.timeline .timeline__quad {
  width: 14px;
  height: 14px;
  background-color: transparent;
  border: 1px solid #000000;
  border-radius: 3px;
}

.header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 2;
}
@media (min-width: 1260px) {
