.header-desk__name::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 20%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid #000000;
  box-shadow: 0 -1px 0 0 #000000 inset;
  transition: all 0.3s ease;
}

.header-desk__name:hover::after {
  opacity: 1;
  bottom: -2px;
}

.header-desk__name--color::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 20%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid #f4a9c0;
  box-shadow: 0 -1px 0 0 #f4a9c0 inset;
  transition: all 0.3s ease;
}

.header-desk__name--color:hover::after {
  opacity: 1;
  bottom: -2px;
}

