  }
}
.accordion-card__content {
  max-height: 0;
  overflow: hidden;
  padding-top: 20px;
  transition: max-height 0.35s;
}
.accordion-card__text {
  font-size: 12px;
  font-weight: 300;
  line-height: 1.5;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: flex-start;
  gap: 20px;
}
